* {
  box-sizing: border-box;
}

.app-wrapper {
  display: grid;
  width: 1200px;
  margin: 0 auto;

  grid-template-areas:
    'h h'
    'n c'
    'f f';

  grid-template-rows: 60px 1fr 50px;
  grid-template-columns: 2fr 10fr;
}

.app-wrapper-content {
  grid-area: c;
  background: rgb(30, 34, 148);
}
