.footer {
  width: 100%;
  display: grid;
  background: gray;
  grid-area: f;

  grid-template-columns: 400px 1fr;
  grid-row: 100px 150px;
  grid-template-areas:
    "logo copy"
    "info info";
}

.footer div:first-child {
  background-color: goldenrod;
  grid-area: logo;
  color: black;
}

.footer div:nth-child(2) {
  background: blueviolet;
  grid-area: copy;
  color: black;
}

.footer div:last-child {
  background: brown;
  grid-area: info;

  color: black;
}
